body {
  background-image: -webkit-linear-gradient(351deg, #ff70a6, #ff9770 40%, #ffd670 73%, #e9ff70);
  background-image: -o-linear-gradient(351deg, #ff70a6, #ff9770 40%, #ffd670 73%, #e9ff70);
  background-image: linear-gradient(99deg, #ff70a6, #ff9770 40%, #ffd670 73%, #e9ff70);
}

#auth-container {
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.auth-login {
  width: 500px;
  height: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 8px;
  background-image: -webkit-linear-gradient(322deg, rgba(64, 61, 57, 0.1), rgba(255, 255, 255, 0.2));
  background-image: -o-linear-gradient(322deg, rgba(64, 61, 57, 0.1), rgba(255, 255, 255, 0.2));
  background-image: linear-gradient(128deg, rgba(64, 61, 57, 0.1), rgba(255, 255, 255, 0.2));
  -webkit-box-shadow: 0 14px 28px 0 rgba(0, 0, 0, 0.25), 0 10px 10px 0 rgba(0, 0, 0, 0.26);
          box-shadow: 0 14px 28px 0 rgba(0, 0, 0, 0.25), 0 10px 10px 0 rgba(0, 0, 0, 0.26);
}

.auth-login-upper {
  margin-top: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.auth-login-lower {
  margin-bottom: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

img {
  width: 120px;
  height: 114px;
  margin-bottom: 40px;
}
h1 {
  font-family: Open Sans,sans-serif;
  font-weight: 700;
  color: white;
  font-size: 100px;
  margin: 0px;
}

.auth-button {
  border-radius: 5px;
  background-color: #403D39;
  /*background-image: radial-gradient(circle at 50% -46%, #737373, #403d39);
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.2);*/
  color: white;
  font-family: Open Sans,sans-serif;
  font-weight: 400;
  font-size: 20px;
  padding: 10px 100px;
}



